@font-face {
  font-family: "SegoeUI";
  src: url(../fonts/SegoeUI/SegoeUI-SemiBold.ttf);
  font-weight: 600;
}

@font-face {
  font-family: "SegoeUI";
  src: url(../fonts/SegoeUI/SegoeUI-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Whitney";
  src: url(../fonts/Whitney/Whitney-Book.ttf);
  font-weight: 350;
}

@font-face {
  font-family: "Whitney";
  src: url(../fonts/Whitney/Whitney-Medium.ttf);
  font-weight: 500;
}

@font-face {
  font-family: "Whitney";
  src: url(../fonts/Whitney/Whitney-Semibold.ttf);
  font-weight: 600;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: "Whitney";
}

.container {
  max-width: 1170px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1170px) {
  .container {
    max-width: 920px;
  }
}

@media (max-width: 920px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.icon::before {
  font-family: "fontawesome";
  content: "\f0c9";
  font-weight: 300;
}

.btn {
  color: #fff;
  background: #F25B24;
  font-weight: 600;
  font-size: 18px;
  line-height: 18px;
  text-align: center;
  display: inline-block;
  border-radius: 4px;
  padding: 16px 60px;
  border: none;
}

@media (max-width: 920px) {
  .btn {
    padding: 15px 30px;
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .btn {
    padding: 5px 10px;
    font-size: 12px;
    width: max-content;
  }
}

.header {
  background: linear-gradient(90deg, #2D1686 0%, #4E33B5 100%);
  position: relative;
}

.header__info {
  padding-top: 53px;
  padding-bottom: 103px;
  display: flex;
  justify-content: space-between;
  height: 100%;
  position: relative;
}

@media (max-width: 768px) {
  .header__info {
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;
  }
}

.header__img {
  max-width: 689px;
  width: 100%;
  height: 472px;
  height: 100%;
  margin-left: -76px;
}

@media (max-width: 1170px) {
  .header__img {
    width: 50%;
    height: 50%;
  }
}

@media (max-width: 768px) {
  .header__img {
    width: 80%;
    margin-left: 0;
  }
}

.header__content {
  min-width: 527px;
  width: 100%;
}

@media (max-width: 1170px) {
  .header__content {
    min-width: 0;
    width: 50%;
  }
}

@media (max-width: 768px) {
  .header__content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
  }
}

.header__title {
  margin-top: 43px;
  color: #fff;
  font-size: 50px;
  line-height: 60px;
  font-weight: 600;
  text-align: left;
}

@media (max-width: 1170px) {
  .header__title {
    font-size: 35px;
    line-height: 40px;
    margin-top: 20px;
  }
}

@media (max-width: 920px) {
  .header__title {
    margin-top: 10px;
  }
}

@media (max-width: 768px) {
  .header__title {
    text-align: center;
  }
}

.header__text {
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;
  color: #fff;
  margin-top: 30px;
  margin-bottom: 70px;
}

@media (max-width: 1170px) {
  .header__text {
    font-size: 20px;
    line-height: 25px;
    margin-bottom: 20px;
  }
}

@media (max-width: 768px) {
  .header__text {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 30px;
    text-align: center;
  }
}

.nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.nav a {
  color: #fff;
}

.nav__burger {
  background: none;
  border: none;
  color: #fff;
  font-size: 30px;
  outline: none;
  line-height: 40px;
  padding: 30px 0;
  visibility: hidden;
}

@media (max-width: 576px) {
  .nav__burger {
    visibility: visible;
  }
}

.menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 37px;
  padding-bottom: 38px;
}

@media (max-width: 576px) {
  .menu {
    position: absolute;
    right: 20px;
    top: 50px;
    flex-direction: column;
    min-width: 200px;
    transform: scaleY(0);
    transition: 0.5s;
    transform-origin: 50% 0 0;
    opacity: 0;
  }
  .menu.active {
    transform: scaleY(1);
    opacity: 1;
  }
}

.menu__link {
  font-weight: 350;
  display: flex;
  align-items: center;
  margin-left: 99px;
  color: #fff;
  line-height: 23px;
}

@media (max-width: 920px) {
  .menu__link {
    margin-left: 50px;
  }
}

@media (max-width: 576px) {
  .menu__link {
    flex-direction: column;
    text-align: right;
    margin-bottom: 20px;
  }
}

.account {
  margin-right: 15px;
}

.account-link {
  display: flex;
  align-items: center;
}

.advantage {
  margin: 130px auto;
}

@media (max-width: 1170px) {
  .advantage {
    margin: 60px auto;
  }
}

@media (max-width: 576px) {
  .advantage {
    margin: 30px auto;
  }
}

.advantage__title {
  font-size: 40px;
  line-height: 52px;
  color: #2E2D35;
  font-family: "SegoeUI";
  font-weight: 600;
  text-align: left;
  margin-bottom: 50px;
}

@media (max-width: 1170px) {
  .advantage__title {
    font-size: 30px;
    margin: 0 auto 50px auto;
    line-height: 40px;
  }
}

@media (max-width: 768px) {
  .advantage__title {
    text-align: center;
  }
}

@media (max-width: 576px) {
  .advantage__title {
    margin: 0 auto 30px auto;
  }
}

.advantage__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

@media (max-width: 768px) {
  .advantage__content {
    grid-template-columns: 1fr;
  }
}

.card {
  display: flex;
  background-color: #F6F5F9;
  justify-content: space-between;
  border-radius: 10px;
}

@media (max-width: 1170px) {
  .card {
    width: 100%;
    text-align: center;
  }
}

@media (max-width: 576px) {
  .card {
    width: 90%;
    margin: auto;
  }
}

.card__info {
  color: #474351;
  font-family: "Segoe UI";
  font-weight: 400;
  font-size: 16px;
  line-height: 23px;
  padding: 43px 40px;
}

@media (max-width: 1170px) {
  .card__info {
    width: 60%;
    padding: 23px 20px;
  }
}

@media (max-width: 920px) {
  .card__info {
    width: 50%;
    padding: 15px 15px;
    font-size: 14px;
    line-height: 16px;
  }
}

@media (max-width: 768px) {
  .card__info {
    width: 100%;
    font-size: 16px;
    line-height: 23px;
    padding: 43px 40px;
  }
}

@media (max-width: 576px) {
  .card__info {
    width: 90%;
    font-size: 14px;
    line-height: 16px;
    padding: 15px 15px;
  }
}

.card__title {
  color: #2E2D35;
  font-family: "Segoe UI";
  font-weight: 600;
  font-size: 22px;
  line-height: 29px;
  margin-bottom: 22px;
}

@media (max-width: 920px) {
  .card__title {
    font-size: 16px;
    line-height: 20px;
  }
}

.card__img {
  margin: 21px 10px;
}

@media (max-width: 1170px) {
  .card__img {
    width: 70%;
    height: 80%;
  }
}

@media (max-width: 576px) {
  .card__img {
    width: 60%;
    height: 70%;
  }
}

.registr {
  background: linear-gradient(90deg, #2D1686 0%, #4E33B5 100%);
  display: flex;
  height: 371px;
  height: 100%;
  border-radius: 10px;
  margin-bottom: 30px;
}

@media (max-width: 1170px) {
  .registr {
    height: 70%;
  }
}

.registr__info {
  display: flex;
}

.registr__description {
  padding: 70px 0 70px 70px;
}

@media (max-width: 1170px) {
  .registr__description {
    width: 50%;
    padding: 35px 0 55px 35px;
  }
}

@media (max-width: 768px) {
  .registr__description {
    width: 50%;
    padding: 15px 0 15px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.registr__title {
  color: #fff;
  font-family: "Whitney";
  font-weight: 600;
  font-size: 34px;
  line-height: 45px;
  margin-bottom: 46px;
}

@media (max-width: 1170px) {
  .registr__title {
    font-size: 26px;
    line-height: 30px;
  }
}

@media (max-width: 920px) {
  .registr__title {
    font-size: 20px;
    line-height: 25px;
  }
}

@media (max-width: 768px) {
  .registr__title {
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 20px;
    text-align: center;
  }
}

@media (max-width: 576px) {
  .registr__title {
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 20px;
    text-align: center;
  }
}

.registr__img {
  margin: 19px 0 12px -190px;
}

@media (max-width: 1170px) {
  .registr__img {
    width: 70%;
    height: 90%;
    margin: 10px 0 0 -150px;
  }
}

@media (max-width: 768px) {
  .registr__img {
    margin: 10px 0 0 -17px;
  }
}

@media (max-width: 576px) {
  .registr__img {
    width: 60%;
    height: 70%;
    margin: 10px 0 0 -20px;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: rgba(28, 24, 47, 0.3);
  overflow: hidden;
  display: none;
}

.modal__dialog {
  max-width: 490px;
  margin: auto auto;
}

.modal__content {
  height: 472px;
  background: #fff;
  border-radius: 6px;
  padding: 60px;
  width: 100%;
  position: relative;
}

.modal__close {
  position: absolute;
  color: #918D9A;
  width: 14px;
  height: 14px;
  right: 20px;
  top: 20px;
  border: none;
  cursor: pointer;
}

.modal__title {
  font-size: 40px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 35px;
}

.modal__input-name {
  display: block;
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
  margin-bottom: 10px;
  color: #474351;
}

.modal__input {
  display: block;
  width: 370px;
  font-size: 16px;
  font-weight: 350;
  line-height: 16px;
  margin-bottom: 18px;
  padding: 15px 10px;
  color: #918D9A;
  border: 1px solid #DDDAE7;
  border-radius: 4px;
}

.modal__btn {
  width: 370px;
}

.active {
  display: flex;
}
